import React, { Component } from 'react';
import { COUNTER_ANIMATE } from '../../../../Services/Utils/utils';
import { connect } from "react-redux";

class CollectionAssetCount extends Component {
    render(){
        const {collectionList, colListReq} = this.props
        return <div className='row gx-3 h-100'>
            <div className='col'>
                <div className="card card-custom card-stretch border-0 gutter-b collection_tile">
                    <div className="card-body d-flex flex-column p-xxl-4">
                            <div className="flex-grow-1">
                                <h5 className="text-dark-alt mb-xxl-3 fs-6">My Collections</h5>
                                <div className="text-dark-alt fw-bolder h4 mb-0 counter" id="collectionCounter" data-counter={collectionList.collectionList.length}>{parseInt(collectionList.collectionList.length) > 0 ? COUNTER_ANIMATE(collectionList.collectionList.length, 0.5, "#collectionCounter") : 0}</div>
                            </div>
                        </div>
                </div>
            </div>
            <div className='col'>
                <div className="card card-custom card-stretch border-0 gutter-b assets_tile">
                    <div className="card-body d-flex flex-column p-xxl-4">
                        <div className="flex-grow-1">
                            <h5 className="text-dark-alt mb-xxl-3 fs-6">Total Assets</h5>
                            <div className="text-dark-alt fw-bolder h4 mb-0 counter" id="assetsCounter">{parseInt(collectionList.assetsCount) > 0 ? COUNTER_ANIMATE(collectionList.assetsCount, 0.5, "#assetsCounter") : 0}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        collectionList: state.collection,
    }
}

export default connect(mapStateToProps)(CollectionAssetCount)